import { mapState } from "vuex";
export default {
  name: "tweetTabs",
  computed: {
    ...mapState(["currentUser"]),
  },
  data() {
    return {
      isUsers: true,
    };
  },
  mounted() {
    this.confirmRouter();
  },
  methods: {
    confirmRouter() {
      this.$route.path.includes("other")
        ? (this.isUsers = false)
        : (this.isUsers = true);
    },
  },
};
