import { mapState } from "vuex";
import store from "./../store";
import UserEdit from "../components/UserEdit.vue";
import usersAPI from "./../apis/users";

export default {
  name: "UserCard",
  components: {
    UserEdit,
  },
  computed: {
    ...mapState(["userProfile"]),
  },
  data() {
    return {
      isUsers: true,
      isProcess: false,
      isFollowed: false,
      currentUser: {
        id: "",
      },
      follower: [],
    };
  },
  props: {
    initialPopular: {
      type: Boolean,
      required: true,
    },
  },
  watch: {
    $route: function () {
      this.fetchUserInfo();
    },
    initialPopular: function () {
      this.fetchUserInfo();
    },
  },
  mounted() {
    this.confirmRouter();
    this.fetchUserInfo();
  },
  methods: {
    async fetchUserInfo() {
      const pramsId = this.$route.params.id;
      await this.$store.dispatch("fetchUserInfo", { payload: pramsId });
      // 取得當前使用者的id
      const { id } = store.state.currentUser;
      this.currentUser.id = id;
      // 取得使用者頁面的被追蹤名單
      this.isFollowed = store.state.userProfile.isFollowed;
      // 取得使用者正在追蹤的名單
      this.following = store.state.userProfile.Followings;
      // 取得使用者頁面的被追蹤的狀態True/False
      this.follower = store.state.userProfile.Followers;
    },
    confirmRouter() {
      if (this.$route.path.includes("other")) {
        this.isUsers = false;
      } else if (this.$route.name.includes("users")) {
        this.isUsers = true;
      }
    },
    async deleteFollow(userId) {
      try {
        console.log("執行刪除");
        const { data } = await usersAPI.deleteFollow({ userId });
        console.log(data);

        this.follower = this.follower.map((follower) => {
          if (follower.id !== userId) {
            return follower;
          } else {
            return {
              ...follower,
            };
          }
        });
        this.$emit("update-popular");
        this.isFollowed = false;
      } catch (error) {
        console.log(error);
      }
    },
    async addFollow(id) {
      try {
        console.log("執行追蹤");
        console.log(id);
        const { data } = await usersAPI.addFollow({ id });
        console.log(data);

        this.follower = this.follower.map((follower) => {
          if (follower.id !== id) {
            return follower;
          } else {
            return {
              ...follower,
            };
          }
        });
        this.$emit("update-popular");
        this.isFollowed = true;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
